
.notifications .notification-list-move {
  transition: transform 0.3s, opacity 0.4s;
}
.notifications .notification-list-item {
  display: inline-block;
  margin-right: 10px;
}
.notifications .notification-list-enter-active, .notifications .notification-list-leave-active {
  transition: opacity 0.4s;
}
.notifications .notification-list-enter, .notifications .notification-list-leave-to {
  opacity: 0;
}



